@import '../../styles/vars';
.banner {
    width: 100%;
    background-color: $black;
    max-height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 25px 0 45px;
}

.wrap {
    display: flex;
    align-items: center;
    column-gap: 83px;
}

.img-wrap {
    width: 152px;
}

.img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.title {
    color: #fff;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
}

.logo-wrap {
    width: 133px;
    height: 100%;
}

.logo{
    width: 100%;
    object-fit: cover;
}